import React, { Fragment, useState ,useEffect , useRef } from "react";
import classnames from "classnames";
import axios from "axios";
import {
  Button,
  Card,
  CardHeader,
  CardBody,
  CardFooter,
  CardImg,
  CardTitle,
  Label,
  FormGroup,
  Form,
  Input,
  InputGroupAddon,
  InputGroupText,
  InputGroup,
  Container,
  Row,
  Col
} from "reactstrap";

const RegisterPage = props => {
  document.body.classList.add("register-page");
  const [ toggle, setToggle ] = useState(false)
  const [ squares1to6, setSquares1to6 ] = useState("false")
  const [ squares7and8, setSquares7and8 ] = useState("")
  const dataStorage =  sessionStorage.getItem('email')

  let wrapper = useRef("wrapper");

  const followCursor = event => {
    let posX = event.clientX - window.innerWidth / 2;
    let posY = event.clientY - window.innerWidth / 6;
    setSquares1to6("perspective(500px) rotateY(" +posX * 0.05 +"deg) rotateX(" + posY * -0.05 +"deg)")
    setSquares7and8("perspective(500px) rotateY(" + posX * 0.02 +"deg) rotateX(" + posY * -0.02 + "deg)")
  };
 
  const url = "https://lantool-backend-dev.herokuapp.com/organizer/"
  const username = "admin-backend"
  const password = "717152bb"
  const aut = "Basic YWRtaW4tYmFja2VuZDo3MTcxNTJiYg=="

     const update = async () => {
            try {
                const result = await  axios.get(url,{
                  
                  
                  headers: {
                    'content-type': 'application/json',
                    'Accept': 'application/json',
                  
              
                  }, 
                  auth: {
                    username: username,
                    password: password
                  }, 
            
              })
              console.log('result', result)
            }
          
            catch (error) {
              console.log('error', error)
            }
        
      }
 
  useEffect(() => {
      document.documentElement.scrollTop = 0;
      document.scrollingElement.scrollTop = 0;
      wrapper.current.scrollTop = 0;
      document.documentElement.addEventListener("mousemove", followCursor)
      return
  });

  useEffect(() => {
    return () => {
      document.body.classList.remove("register-page");
      document.documentElement.removeEventListener( "mousemove", followCursor);
    }
  }, []);

  const handleClick = e => {
    e.preventDefault()
    update()


  }
  
  return ( 
    <Fragment>
      <div className="wrapper" ref={wrapper}>
          <div className="page-header">
            <div className="page-header-image" />
            <Container>
              <Row>
                <Col className="mx-auto" lg="5" md="12">
                    <div
                      className="square square-7"
                      id="square7"
                      style={{ transform: squares7and8 }}
                    />
                    <div
                      className="square square-8"
                      id="square8"
                      style={{ transform: squares7and8 }}
                    />
                  <Card className="card-register">
                    <CardHeader>
                      <CardImg
                        alt="..."
                        src={require("../../assets/img/square1.png")}
                      />
                      <CardTitle tag="h4">Register</CardTitle>
                    </CardHeader>
                    <CardBody>
                      <Form className="form">
                        <InputGroup 
                          className={classnames({"input-group-focus": toggle.emailFocus})}>
                          <InputGroupAddon addonType="prepend">
                            <InputGroupText>
                              <i className="tim-icons icon-email-85" />
                            </InputGroupText>
                          </InputGroupAddon>
                          <Input
                            value={dataStorage === "undefined" ? null : dataStorage}
                            placeholder="Email"
                            type="text"
                            onFocus={e => setToggle( { emailFocus: true })}
                            onBlur={e =>  setToggle( { emailFocus: false })}
                          />
                        </InputGroup>
                        <InputGroup
                          className={classnames({"input-group-focus": toggle.passwordFocus})}>
                          <InputGroupAddon addonType="prepend">
                            <InputGroupText>
                              <i className="tim-icons icon-lock-circle" />
                            </InputGroupText>
                          </InputGroupAddon>
                          <Input
                            placeholder="Password"
                            type="text"
                            onFocus={e => setToggle( { passwordFocus : true })}
                            onBlur={e =>  setToggle( { passwordFocus: false })}
                            
                          />
                        </InputGroup>
                        <InputGroup
                          className={classnames({"input-group-focus": toggle.passwordFocusConfirmation})}
                        >
                          <InputGroupAddon addonType="prepend">
                            <InputGroupText>
                              <i className="tim-icons icon-lock-circle" />
                            </InputGroupText>
                          </InputGroupAddon>
                          <Input
                            placeholder="Password confirmation"
                            type="text"
                            onFocus={e => setToggle( { passwordFocusConfirmation: true })}
                            onBlur={e =>  setToggle( { passwordFocusConfirmation: false })}
                          />
                        </InputGroup>

                        <FormGroup check className="text-left ml-0 mt-5">
                          <Label check>
                            <Input type="checkbox" className=""/>
                            <span style={{fontSize: "0.75rem"}}className="form-check-sign" />Je suis organisateur d'événement. Je ne souhaite pas acheter ou revendre un billet.{" "}
                          </Label>
                        </FormGroup>
                      </Form>
                    </CardBody>
                    <CardFooter className="mt-0 pt-0">
                    <Button
                        className="btn-info w-100 btn-simple pl-2 pr-2"
                        color="info"
                        href="#pablo"
                        onClick={e => handleClick(e)}
                        size="lg"
                      >
                        Get Started
                      </Button>
                  
                      <p>Déjà un compte organisateur ? <a href="/login">Login</a></p>
                    </CardFooter>
                  </Card>
                </Col>
              </Row>
            </Container>
            <div className="register-bg" />
            <div
              className="square square-1"
              id="square1"
              style={{ transform: squares1to6 }}
            />
            <div
              className="square square-2"
              id="square2"
              style={{ transform: squares1to6 }}
            />
            <div
              className="square square-3"
              id="square3"
              style={{ transform: squares1to6 }}
            >
            </div>
            <div
              className="square square-4"
              id="square4"
              style={{ transform: squares1to6 }}
            />
            <div
              className="square square-5"
              id="square5"
              style={{ transform: squares1to6 }}
            />
            <div
              className="square square-6"
              id="square6"
              style={{ transform: squares1to6 }}
            />
          </div>
        </div>
    </Fragment>
   );

}
 
export default RegisterPage;
// export default withRouter(RegisterPage);