import React, { Fragment, useState } from "react";
import {Button, Col, Row} from "reactstrap";

const FirstPageHeader = () => {
  const [ inputEmail, setInputEmail ] = useState({
    email: null,
    errorEmail: null
  })
  const dataStorage =  sessionStorage.getItem('email')
  
  const handleSubmit = e => {
    sessionStorage.setItem("email", inputEmail.email)
  }

  const handleChange = e => {
    const { name, value } = e.target
    setInputEmail({...inputEmail, [name]: value})
  }

  const handleClick = e => {
    console.log('inputEmail', inputEmail)
    if (inputEmail.email === null) {
      e.preventDefault();
      setInputEmail({...inputEmail,errorEmail: "email required" })
    }
  }

  return ( 
    <Fragment> 
      <div className="page-header">
        <img alt="..." className="path" src={require("../../assets/img/blob.png")} />
        <img alt="..." className="path2" src={require("../../assets/img/path2.png")} />
        <img alt="..." className="shapes triangle" src={require("../../assets/img/triunghiuri.png")} />
        <img alt="..." className="shapes wave"src={require("../../assets/img/waves.png")} />
        <img alt="..." className="shapes squares" src={require("../../assets/img/patrat.png")} />
        <img alt="..." className="shapes circle" src={require("../../assets/img/cercuri.png")} />

        <div className="container" >
          <div className="content-center brand">
            <h1 className="h1-seo "> INTRA <small className="text-warning">Tools </small></h1>
            <h3> Your esport tool for big and small Lan </h3>
            <form action="/register" onSubmit={(e) => handleSubmit(e)}> 
              <Row className="row-input col-lg-12">
                <Col className="pr-0" lg="8" md="12" xs="12">
                  <input
                    value={inputEmail.email}
                    className="emailInput form-control"
                    aria-label="Your email"
                    id="signupSrEmail"
                    name="email"
                    placeholder="Your email"
                    type="email"
                    onChange={(e) => handleChange(e)}
                  />
                  { inputEmail.errorEmail !== null ? <p> {inputEmail.errorEmail }</p> : null }
                </Col>
                <Col className="pl-0" lg="4" md="12" xs="12">
                <Col  md="2" xs="2"></Col>
                  <Col  md="auto" xs="auto">
                    <button
                      onClick={(e) => handleClick(e)}
                      type="submit"
                      style={{fontSize: "0.8em"}}
                      className="btn-warning auto btn-simple pl-2 pr-2">
                     Create a free account
                    </button>
                  </Col>
                <Col  md="2" xs="2"></Col>
                </Col>
                  <Button
                    className="col-lg-12 btn-link mt-4 d-block"
                    color="warning"
                    href="/"
                  >
                  <u>Live demo</u>
                </Button>
              </Row>
              </form>
          </div>
        </div>
      </div>
    </Fragment>
   );
}

export default FirstPageHeader;










