import { GET_EMAIL_INPUT } from "../actions/registerAction"
const initState = {
    email: ""
};
  
const registerReducer = (state = initState, action) => {
    

    switch(action.type) {
        case 'GET_EMAIL_INPUT':
            console.log('action', action.payload)
            return {
                ...state,
                email: action.payload
            }


        default:
            return state;
    }
};

export default registerReducer;